encia Artificial”, marcando el inicio formal de la disciplina.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"1960 - 1970","titulo":"La era dorada y el primer invierno"}},[_c('div',{staticClass:"col-md-11"},[_c('p',[_vm._v("Las décadas de 1960 y 1970 fueron consideradas la primera gran etapa de auge de la IA. En este periodo se desarrollaron programas capaces de resolver problemas matemáticos y lógicos, así como sistemas pioneros de procesamiento de lenguaje natural. Entre ellos destacó ELIZA (1966), que simulaba conversaciones con un terapeuta, y SHRDLU, capaz de comprender y ejecutar instrucciones en entornos virtuales limitados. Sin embargo, las expectativas superaron a los logros reales y la tecnología no alcanzó el rendimiento esperado. Esto dio paso al primer “invierno de la IA”, caracterizado por la reducción de la financiación y el desinterés institucional. ")])])]),_c('div',{staticClass:"row",attrs:{"numero":"1980 - 1990","titulo":"El resurgimiento y los sistemas expertos"}},[_c('div',{staticClass:"col-md-11"},[_c('p',[_vm._v("En la década de 1980 la IA experimentó un resurgimiento gracias a los sistemas expertos, diseñados para reproducir la toma de decisiones en áreas específicas. Uno de los más destacados fue MYCIN, orientado al diagnóstico médico de enfermedades infecciosas. En 1986, Rumelhart, Hinton y Williams popularizaron el algoritmo de retro propagación, que permitió entrenar redes neuronales de manera más eficaz. Sin embargo, hacia finales de la década, los altos costos y limitaciones técnicas provocaron el segundo “invierno de la IA”. El campo recuperó impulso en los años 90, con avances en el aprendizaje automático, el reconocimiento de voz y el desarrollo de nuevas técnicas como las máquinas de soporte vectorial. En 1997, la IA alcanzó un hito histórico cuando Deep Blue, de IBM, derrotó al campeón mundial de ajedrez Garry Kasparov, demostrando la capacidad de las máquinas para superar a los humanos en tareas altamente complejas. ")])])]),_c('div',{staticClass:"row",attrs:{"numero":"2000","titulo":"La revolución de los datos "}},[_c('div',{staticClass:"col-md-11"},[_c('p',[_vm._v("Con la llegada de internet y el acceso a grandes volúmenes de información, la IA entró en una fase de expansión sin precedentes. El auge del "),_c('em',[_vm._v("big data")]),_vm._v(" y la mayor capacidad de procesamiento facilitaron notables avances en el aprendizaje automático. En 2006, Geoffrey Hinton revitalizó el concepto de aprendizaje profundo ("),_c('em',[_vm._v("Deep Learning")]),_vm._v("), que pronto se convertiría en la base de los sistemas más modernos. En paralelo, surgieron aplicaciones prácticas como la traducción automática, el reconocimiento de voz y los primeros proyectos de vehículos autónomos (Google, 2009). En 2011, el sistema Watson de IBM ganó el concurso Jeopardy, superando a los mejores jugadores humanos y mostrando avances impresionantes en el procesamiento del lenguaje natural. ")])])]),_c('div',{staticClass:"row",attrs:{"numero":"2010","titulo":"La expansión cotidiana de la IA"}},[_c('div',{staticClass:"col-md-11"},[_c('p',[_vm._v("Durante la década de 2010, la inteligencia artificial comenzó a integrarse en la vida diaria. En 2012, el modelo AlexNet revolucionó la visión por computadora al ganar la competencia ImageNet, gracias al uso de redes neuronales profundas. Poco después, en 2016, AlphaGo de DeepMind venció al campeón mundial de Go, un juego considerado mucho más complejo que el ajedrez. En esos mismos años, se popularizaron los asistentes virtuales como Siri, Alexa y Google Assistant, además de los sistemas de recomendación que hoy dominan plataformas de música, cine y redes sociales. En 2017, Google introdujo la arquitectura de los "),_c('em',[_vm._v("Transformers")]),_vm._v(", que dio origen a modelos de lenguaje de última generación como BERT y, posteriormente, GPT.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"2020","titulo":"La era generativa y el presente"}},[_c('div',{stati