irar su reloj inteligente. Revisa la hora, los mensajes y su ritmo cardíaco. Detrás de esa rutina hay un sistema que aprende de sus hábitos y predice patrones. Hace unos años parecía ciencia ficción; hoy pasa inadvertido.")])]),_c('div',{staticClass:"col-lg-5 col-md-6"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/t1/37.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-6 col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Recomendaciones a la carta")]),_c('p',[_vm._v("Por la tarde, enciende Netflix. En la pantalla aparece: “Porque viste Mindhunter, te recomendamos Orange Is the New Black”. Sin pensarlo demasiado, da clic. Ese motor de recomendaciones, también presente en Spotify o Amazon, filtra miles de opciones para ofrecerle lo que probablemente disfrute. ")])]),_c('div',{staticClass:"col-lg-5 col-md-6"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/t1/38.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-6 col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Creatividad en un toque")]),_c('p',[_vm._v("Más tarde, revisa fotos de sus vacaciones. En una, un desconocido arruina el fondo. Usa la opción de “Borrador mágico” y, con un toque, la IA elimina al intruso y reconstruye la imagen. Algo que antes requería horas en Photoshop, hoy se resuelve en segundos desde el celular.")])]),_c('div',{staticClass:"col-lg-5 col-md-6"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/t1/39.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-6 col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Jugando con algoritmos")]),_c('p',[_vm._v("Antes de dormir, MarIA juega una partida de ajedrez contra la computadora. Lo toma como un pasatiempo, pero detrás hay un programa capaz de predecir movimientos y